import "./index.css";
var Logo = require("./assets/sun_icon.png");
// import { MyClass } from "./example-unit";
// const a = new MyClass(2);
// console.log("number is", a.get());
var time = document.querySelector("#display");
var image = document.querySelector("#image");
image.src = Logo;
var ClockBuilder = /** @class */ (function () {
    function ClockBuilder(divClock) {
        this.date = new Date();
        this.offsetHours = 0;
        this.offsetMin = 0;
        this.displayClock(divClock);
    }
    /* This method custom the clock loop */
    ClockBuilder.prototype.displayClock = function (divClock) {
        var _this = this;
        var currentClock = new Date();
        this.setClockTime(currentClock);
        divClock.innerHTML = this.getTimeText(this.date.getHours(), this.date.getMinutes(), this.date.getSeconds());
        setTimeout(function () { return _this.displayClock(divClock); }, 300);
    };
    /* This method set clock time according to the offset introduced by funtionalities  */
    ClockBuilder.prototype.setClockTime = function (currentDate) {
        this.date.setHours(currentDate.getHours() + this.offsetHours);
        this.date.setMinutes(currentDate.getMinutes() + this.offsetMin);
        this.date.setSeconds(currentDate.getSeconds());
        return this;
    };
    /* Visualization methods */
    ClockBuilder.prototype.getTimeText = function (hours, minutes, seconds) {
        return this.checkTime(hours % 24) + " h " + this.checkTime(minutes % 60) + " m " + this.checkTime(seconds % 60) + " s";
    };
    ClockBuilder.prototype.checkTime = function (time) {
        return time < 10 ? "0" + time : time;
    };
    return ClockBuilder;
}());
var clock = new ClockBuilder(time);
